import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    appTitle: 'Room Inspection',
    previousRoute: '#',
    isSearchVisible: false

  },
  mutations: {
    'SET_TITLE' (state, title) {
      state.appTitle = title
    },
    'SET_PREVIOUS_ROUTE' (state, route) {
      state.previousRoute = route
    },
    'TOGGLE_SEARCH' (state) {
      state.isSearchVisible = !state.isSearchVisible
    }
  },
  actions: {
    setTitle: (context, title) => {
      context.commit('SET_TITLE', title)
    },
    setPreviousRoute: (context, route) => {
      context.commit('SET_PREVIOUS_ROUTE', route)
    },
    toggleSearch: (context) => {
      context.commit('TOGGLE_SEARCH')
    }
  },
  getters: {
    appTitle: state => {
      return state.appTitle
    },
    previousRoute: state => {
      return state.previousRoute
    },
    searchVisible: state => {
      return state.isSearchVisible
    }
  }
})
