    <v-layout wrap>
        <v-flex xs12>
            <v-card>
                <v-card-title><h4>รายละเอียดการแจ้งซ่อม</h4></v-card-title>
                <!-- <v-divider></v-divider> -->
                <v-list dense>
                    <v-list-tile>
                        <v-list-tile-content>Location:</v-list-tile-content>
                        <v-list-tile-content class="align-end">{{ data.location }}</v-list-tile-content>
                    </v-list-tile>
                    <v-list-tile>
                        <v-list-tile-content>จุดที่ชำรุด:</v-list-tile-content>
                        <v-list-tile-content class="align-end">{{ data.object }}</v-list-tile-content>
                    </v-list-tile>
                    <v-list-tile>
                        <v-list-tile-content>งาน:</v-list-tile-content>
                        <v-list-tile-content class="align-end">{{ data.job }}</v-list-tile-content>
                    </v-list-tile>
                    <v-list-tile>
                        <v-list-tile-content>รายละเอียด:</v-list-tile-content>
                        <v-list-tile-content class="align-end">{{ data.detail }}</v-list-tile-content>
                    </v-list-tile>
                    <v-list-tile>
                        <v-list-tile-content>หมายเหตุ:</v-list-tile-content>
                        <v-list-tile-content class="align-end">{{ data.remark }}</v-list-tile-content>
                    </v-list-tile>
                    <v-flex xs10 offset-xs1>
                        <v-select v-model="status" :items="status_items" label="สถานะ"></v-select>
                    </v-flex>
                </v-list>
                <v-container
                    fluid
                    grid-list-lg
                >
                    <v-layout row wrap>
                        <v-flex xs12 sm6>
                            <v-card>
                                <v-card-title>
                                <div>
                                    <span class="grey--text">รูปถ่ายหลังซ่อม</span><br>
                                </div>
                                </v-card-title>
                                <v-flex xs10 offset-xs1 v-if="!hasImage">
                                    <v-btn block round outline large color="primary" 
                                        onclick="document.getElementById('imagePicker').click()" 
                                        class="font-weight-bold subheading">
                                        <v-icon left>camera_alt</v-icon>
                                        ถ่ายภาพ
                                    </v-btn>
                                </v-flex>
                                <v-flex xs10 offset-xs1 v-if="hasImage">
                                    <img :src="image" :style="{width: '100%'}">
                                    <v-btn slot="activator" block flat 
                                        color="error" 
                                        @click="dialog = true" 
                                        class="font-weight-bold subheading">
                                        <v-icon left>delete</v-icon>
                                        ลบรูป
                                    </v-btn>
                                </v-flex>
                            </v-card>
                        </v-flex>
                    </v-layout>
                </v-container>
                
            
                <v-dialog v-model="dialog" persistent :max-width="dialogSize">
                    <v-card>
                        <v-card-title class="headline">คุณยืนยันที่จะลบรูปนี้ ?</v-card-title>
                        <!-- <v-card-text></v-card-text> -->
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="green darken-1" flat class="font-weight-bold subheading" @click.native="dialog = false">ยกเลิก</v-btn>
                            <!-- <v-btn color="green darken-1" flat @click.native="dialog = false">Agree</v-btn> -->
                            <v-btn flat color="green darken-1" @click="removeImage" class="font-weight-bold subheading">
