                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
                
                <image-uploader 
                    ref="imagePicker" 
                    id="imagePicker" 
                    :maxSize="0.1" 
                    :autoRotate="true" 
                    outputFormat="string" 
                    :preview="false" 
                    capture="camera" 
                    @input="setImage" 
                    v-show="false">
                </image-uploader>
                <!-- </v-btn> -->
                <v-spacer></v-spacer>
                <v-flex xs10 offset-xs1 class="pb-2 mb-2">
                    <v-btn large block color="success" 
                        class="font-weight-bold subheading" 
