<script>
    import { ImageUploader } from 'vue-image-upload-resize'
    import UploadButton from 'vuetify-upload-button'
    import { mapActions } from "vuex";
    import moment from 'moment'

    export default {
        data() {
            return {
                data: {
                    location: 'ห้องนอน 1(ใหญ่)',
                    object: 'หน้าต่าง',
                    job: 'ทั่วไป',
                    detail: 'ทำความสะอาด',
                    remark: ''
                },
                status: '',
                status_items: ['เสร็จเรียบร้อยแล้ว', 'ตรวจรับแล้ว', 'ยกเลิก'],
                isAccept: false,
                isNotAccept: false,
                option:{
                    penColor:"rgb(0, 0, 0)",
                    backgroundColor:"rgb(255,255,255)"
                },
                datePicker: null,
                menu: false,
                hasImage: false,
                image: '',
                dialog: false
            };
        },
        created() {
            this.changeTitle("ตรวจงานแจ้งซ่อม");
            this.changePreviousRoute("inspection");
            // console.log('created',this.$store.state.appTitle);
        },
        computed: {
            computedDateFormatted () {
            var result
                if(this.datePicker){
                    result = moment(this.datePicker).format('DD/MM/YYYY')
                } else {
                    result = moment().format('DD/MM/YYYY')
                }

                return result
                
            },
            dialogSize() {
                console.log(window.innerWidth - (window.innerWidth * 0.1))
                return window.innerWidth - (window.innerWidth * 0.1)
            }
        },
        methods: {
            ...mapActions({
                changeTitle: "setTitle",
                changePreviousRoute: "setPreviousRoute"
            }),
            imageSelected(fileObject) {
                console.log('image selected', fileObject);
            },
            setImage: function(file) {
                this.hasImage = true
                console.log(file);
                this.image = file
    
            },
            removeImage() {
                this.hasImage = false
                this.image = ''
                this.dialog = false
                document.getElementById("imagePicker").value = null;
            },clear(){
                var _this = this;
                _this.$refs.signature.clear();
            },
            undo(){
                var _this = this;
                _this.$refs.signature.undo();
            },
            save() {
                var _this = this;
                // var png = _this.$refs.signature.save()
                var jpeg = _this.$refs.signature.save('image/jpeg')
                // var svg = _this.$refs.signature.save('image/svg+xml');
                this.signature = jpeg;
                this.hasSignature = true;
                this.showSignPad = false;
            }
        },
        watch: {
            isAccept(){
                this.isNotAccept = !this.isAccept 
            },
            isNotAccept(){
                this.isAccept  = !this.isNotAccept
            }
        },
        components: {
            'upload-btn': UploadButton,
            ImageUploader
        }
    };
</script>
